body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: linear-gradient(90deg, #74c6e7, #44a8d1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.app {
  background: #ffffff;
  width: 60vw;
  height: 60vh;
}

.margin {
  margin-left: 5rem;
  margin-right: 5rem;
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.padding {
  padding-left: 5rem;
  padding-right: 5rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}

.text-center {
  text-align: center;
}

.fw-bold {
  font-weight: bold;
}
